<template>
<div class="issueListBox">
  <h4 class="lz-title-h4">请确认您的告知异常选项</h4>
  <div v-for="(list,i) in intell.issList">
    <div class="text">{{i+1}}：{{list.diseaseDesc}}</div>
    <div class="question" v-if="list.arrgeMent" v-for="(item,n) in list.arrgeMent">Q{{n+1}}：{{item.diseaseName}}
      <p v-for="(val,s) in item.subDiseaseList" v-if="val.check">A{{n+1}}：{{val.name}}
      </p>
    </div>
  </div>
  <div class="btn">
    <div class="lz-isIndex" @click="resultFun(1)">已完成健康告知</div>
  </div>
    <div class="lz-isIndex" @click="resultFun(2)">还有其他疾病需要反馈</div>
   <div class="lz-isIndex" @click="resultFun(0)">上一步</div>
    <div style="color: red" class="padding-top-30" align="center">注:如有答题需回到上一题,请点击“上一步”</div>
</div>
</template>
<script>
  import Vue from "vue";
  import motheds from "@/api/motheds";
  import { mapMutations } from "vuex";
  import { mapGetters } from "vuex";
  var vm;
  export default {
    name: "Issue",
    data() {
      return {
        resultdata:[],
        diseaseCode:''
      };
    },
    created() {
      vm = this;
      this.getQuery()
    },
    methods: {
      getQuery(){
        this.desLevel = parseInt(this.$route.params.desLevel);
        this.intell.diseseList.splice(this.desLevel+1) //删掉当前等级之后的疾病列表
        this.intell.issList.splice(this.desLevel+1); //删除当前等级之后的问题列表
        this.set_msg({key: "intelligent", data: this.intell});
      },
      //type 0后退 1确认 2返回疾病列表
      resultFun(type) {
        switch (type) {
          case 0:
            this.$router.go(-1)
            break;
          case 1: 
            if(this.intell.results.result == 2){
              vm.$router.push({name: "Result1"});
            }else{
              this.handleEntry()
            }
            break;
          case 2:
            if(this.intell.results.result == 2){
              vm.$router.push({name: "Result1"});
            }else{
              this.$router.push({ path:"/BNIndex/0/"+(this.desLevel+1)})
            }
            break;
        }
      },
      handleEntry(){
        let diseaseList = {}
        this.intell.issList.forEach((v,i) => {
          diseaseList[i] = v.paramsDate
        });
        this.$http.getHttp({
          name: "deseaseCommit",
          params: {
            paperId:this.intell.paperId,
            diseaseList:diseaseList
          },
          code: this.intell.int_quey.code
        },res => {
          vm.intell.results.resultInfo =res.data.result
          if(res.data.success == "true"){
            // S:承保  Q:转人工核保  EX:除外责任  D:拒保
            if(res.data.type == "S")vm.intell.results.result = 0;
            if(res.data.type == "EX")vm.intell.results.result = 1;
            if(res.data.type == "Q")vm.intell.results.result = 5; 
          }else{
              vm.intell.results.result = 2;
          }
          vm.set_msg({ key: "intelligent",lev:"results", data: vm.intell.results });
          vm.$router.push({name: "Result1"});
        });
      },
       //传入后台的结果过滤
      dessFilter(){
        var self = this;
        _.forEach(this.intell.issList,(v,i) => {
          self.resultdata[i] = {};
          self.resultdata[i].diseaseCode = v.diseaseCode; //疾病code
          self.resultdata[i].diseaseDesc = v.diseaseDesc; //疾病名称
          if(v.result){
            //无问题
            self.resultdata[i].result = {};
            self.resultdata[i].result.type = v.result;
            v.result == '1' && v.resultDesc? self.resultdata[i].result.des= v.resultDesc:null //除则时存储除则信息
          }else{
            // 有疾病问题
            self.resultdata[i].questAry=[]; //问题答案初始化
            _.forEach(v.arrgeMent,(e,n)=>{
              self.resultdata[i].questAry[n]={};
              _.forEach(e.questionData,(r,s)=>{
                // 遍历问题 获取问题答案
                if(r.check){
                  self.resultdata[i].questAry[n].code = r.code;
                  self.resultdata[i].questAry[n].name = r.name;
                  if(r.result){ 
                    //存在result 保存result
                    self.resultdata[i].result = {};
                    self.resultdata[i].result.type = r.result;
                    r.result == '1' && r.resultDesc? self.resultdata[i].result.des = r.resultDesc:null;//除则时存储除则信息
                  }
                }
              })
            })
          }
        });
        console.log(this.resultdata)
      },
       ...mapMutations({
        set_msg: "SET_MESSAGE"
      })
    },
    computed: {
      // 使用对象展开运算符将 getter 混入 computed 对象中
      ...mapGetters([
        "intell"
        // ...
      ])
    },
    components: {},
    mounted() {
      console.log(this.issList);
    }
  };
</script>

<style lang="scss">
.issueListBox {
  font-size: 16px;
  background-color: #fff;
  min-height: 100%;
  .btn {
    overflow: hidden;
    margin: 20px 0;
  }
  .text {
    display: inline-block;
    padding: 15px 0 0 15px;
    font-weight: bold;
  }
  .question {
    padding: 15px;
    line-height: 1.5;
    border-bottom: 1px solid #ccc;
  }
}
</style>


